* {
  margin: 0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
}

.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
  margin: 0;
  top: 0;
  height: 60px;
  z-index: 100;
  /* max-width: 1500px; */
  padding: 10px 50px;
}

.header > p {
  font-size: 20px;
  font-weight: 600;
  margin-top: 0;
  letter-spacing: 0.3px;
  color: #000000;
  text-transform: uppercase;
}

.header__logo {
  position: relative;
  flex-direction: column;
  color: white;
  background-color: white;
  width: 60px;
  height: 50px;
  overflow: hidden;
  padding: 8px;
  z-index: 0;
  border-radius: 6px;
}

.header__logo::before {
  box-sizing: border-box;
  content: "";
  position: absolute;
  z-index: -2;
  left: -50%;
  top: -50%;
  width: 200%;
  height: 200%;
  background-color: #39998a;
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: 0 0, 20% 0, 20% 20%, 0 20%, 30% 30%;
  background-image: linear-gradient(
    red,
    orange,
    yellow,
    green,
    #ff00ff,
    blue,
    violet
  );
  -webkit-animation: rotate 3s linear infinite;
          animation: rotate 3s linear infinite;
}

@-webkit-keyframes rotate {
  100% {
    transform: rotate(1turn);
  }
}

@keyframes rotate {
  100% {
    transform: rotate(1turn);
  }
}

.header__logo::after {
  box-sizing: border-box;
  content: "";
  position: absolute;
  z-index: -1;
  left: 8px;
  top: 8px;
  width: calc(100% - 16px);
  height: calc(100% - 15px);
  background: white;
  border-radius: 6px;
}

.header__logo > table {
  width: 100%;
  height: 100%;
  border-spacing: 0px;
  border-collapse: collapse;
}

.header__logo > table > tr:nth-child(1) > td:nth-child(2) {
  border-bottom: 6px inset #ffdf00;
}
.header__logo > table > tr:nth-child(2) > td:nth-child(1) {
  border-bottom: 6px inset #ffdf00;
}

.home {
  display: flex;
  justify-content: center;
  flex-direction: column;
  max-width: 1200px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
}
.home__row {
  margin-top: 50px;
  margin-bottom: 50px;
}
.home__banner {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 600px;
}

.home__banner > p {
  font-weight: bolder;
  font-family: "Book Antiqua", Georgia, "times new roman", serif;
  text-transform: uppercase;
  font-size: 36px;
  margin-bottom: 20px;
}
