/* ================ The Timeline__list ================ */

h5 {
  font-size: 1.6rem;
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  font-weight: 400;
  line-height: 1.334;
  color: #333;
  letter-spacing: 0em;
}

p {
  margin: 1em 0 0;
  line-height: 1.6;
  font-size: 1.2rem;
  font-weight: 400;
}

h1 {
  margin-bottom: 50px;
  width: 200px;
  margin: 0 auto;
  margin-bottom: 60px;
  padding: 10px;
  text-align: center;
  border-bottom: 6px solid rgba(78, 78, 255, 2);
}

.timeline {
  color: #333;
  display: flex;
  position: relative;
  flex-direction: column;
  overflow: hidden;
  justify-content: center;
  margin-top: 20px;
  padding: 20px 50px;
}
.timeline__itemContainerLeft,
.timeline__itemContainerRight {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom: 100px;
}
.timeline__itemContainerLeft .timeline__itemLeft {
  margin-left: 0;
  position: relative;
  padding: 20px;
  margin-right: auto;
  width: 42%;
  border: 1px solid lightgray;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  border-radius: 5px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2), 0 1px 1px 0 rgba(0, 0, 0, 0.14),
    0 2px 1px -1px rgba(0, 0, 0, 0.12);
}

.timeline__itemContainerLeft .timeline__itemRight {
  margin-right: 0;
  position: relative;
  padding: 20px;
  margin-left: auto;
  width: 42%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  text-align: center;
  border-radius: 5px;
}

.timeline__itemContainerRight .timeline__itemRight {
  margin-right: 0;
  position: relative;
  padding: 20px;
  margin-left: auto;
  width: 42%;
  border: 1px solid lightgray;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  border-radius: 5px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.2), 0 1px 1px 0 rgba(0, 0, 0, 0.14),
    0 2px 1px -1px rgba(0, 0, 0, 0.12);
}

.timeline__itemContainerRight .timeline__itemLeft {
  margin-left: 0;
  position: relative;
  padding: 20px;
  margin-right: auto;
  width: 42%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  text-align: center;
  border-radius: 5px;
}

.timeline:before {
  position: absolute;
  left: 50%;
  content: " ";
  top: 150px;
  display: block;
  width: 6px;
  height: 100%;
  margin-left: -2px;
  background: rgb(80, 80, 80);
  background: -moz-linear-gradient(
    top,
    rgba(80, 80, 80, 0) 0%,
    rgb(80, 80, 80) 8%,
    rgb(80, 80, 80) 92%,
    rgba(80, 80, 80, 0) 100%
  );
}

/* ================ Timeline__list Media Queries ================ */

@media screen and (max-width: 660px) {
  .timeline__list {
    width: 100%;
    padding: 4em 0 1em 0;
  }

  .timeline__list li {
    padding: 2em 0;
  }

  .direction-l,
  .direction-r {
    float: none;
    width: 100%;

    text-align: center;
  }

  .flag-wrapper {
    text-align: center;
  }

  .flag {
    background: rgb(255, 255, 255);
    z-index: 15;
  }

  .direction-l .flag:before,
  .direction-r .flag:before {
    position: absolute;
    top: -30px;
    left: 50%;
    content: " ";
    display: block;
    width: 20px;
    height: 20px;
    margin-left: -9px;
    background: #fff;
    border-radius: 10px;
    border: 4px solid rgb(255, 219, 12);
    z-index: 10;
  }

  .direction-l .flag:after,
  .direction-r .flag:after {
    content: "";
    position: absolute;
    left: 50%;
    top: -8px;
    height: 0;
    width: 0;
    margin-left: -8px;
    border: solid transparent;
    border-bottom-color: rgb(255, 255, 255);
    border-width: 8px;
    pointer-events: none;
  }

  .time-wrapper {
    display: block;
    position: relative;
    margin: 4px 0 0 0;
    z-index: 14;
  }

  .direction-l .time-wrapper {
    float: none;
  }

  .direction-r .time-wrapper {
    float: none;
  }

  .desc {
    position: relative;
    margin: 1em 0 0 0;
    padding: 1em;
    background: rgb(245, 245, 245);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0.2);

    z-index: 15;
  }

  .direction-l .desc,
  .direction-r .desc {
    position: relative;
    margin: 1em 1em 0 1em;
    padding: 1em;

    z-index: 15;
  }
}

@media screen and (min-width: 400px) {
  .direction-l .desc,
  .direction-r .desc {
    margin: 1em 4em 0 4em;
  }
}

